












































import Vue from 'vue'
import axios from 'axios'
export default Vue.extend({
  data() {
    return{
      announcements: []
    }
  },
  beforeMount() {
    axios.get('/slack/announcements')
    .then( (response) => {
      console.log(response);
      this.announcements = response.data
    })
    .catch(function (error) {
      console.log(error);
    });
  },

})
